import { Component, EventEmitter, Input, Output, OnInit } from '@angular/core';
import { CreatorService } from './creator.service';
import { DomSanitizer } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { BlogDetails, BlogParagraph } from './models';
import { AppConstants } from "../app.constants";
/**
 * HTML Structure
 * <div id="hansini"> ==> <main> <main role="main">

 * <H1> Title
 * <div id="summary hidden"> Introduction
 * <div id="mainImage"> Main Image
 * <div id="toc"> Table of Contents
 * <div id="descriptions"> Description
 * <div id="paragraphs">
 *   <h2>
 *   <h3>
 *   <description>
 *   <codeSample>
 *     <file>
 *     <pre><code>
 *   </codeSample>
 *   <list>
 *   <url>
 *   <sourcecoderef>
 *   <lis>
 *
 */
@Component({
  selector: 'blog-post',
  templateUrl: './htmlresult.component.html',
  styleUrls: ['./creator.css', './htmlresult.css']
})
export class HtmlResultComponent implements OnInit {
  DEBUG_INFO: string;
  blogDetails = new BlogDetails();
  public currentPageLSID;
  protected sub: any;
  public HTMLOUTPUT: String;
  //htmlLinkAnchors = new Map();
  htmlLinkAnchors : string[];
  public JSONOUTPUT : String;

  constructor(protected sanitizer: DomSanitizer,
    protected blogService: CreatorService,
    protected router: Router,
    protected activeRouter: ActivatedRoute, protected fb: FormBuilder) {
      this.htmlLinkAnchors = [];
  }

  ngOnInit() {
    this.sub = this.activeRouter.queryParams.subscribe(params => {
      this.currentPageLSID = params['lsid'];
      console.log('route parameter lsid = ' + this.currentPageLSID);
      if (this.currentPageLSID != undefined && this.currentPageLSID != null) {
        console.log('load from LS or Server lsid = ' + this.currentPageLSID);
        let localBlogDetails = this.blogService.findObjectByIDFromLS(this.currentPageLSID);
        if (localBlogDetails != null) {
          console.log('load object from LS =  ' + JSON.stringify(localBlogDetails));
          this.blogDetails = this.blogDetails.decodeBlog(localBlogDetails);

          this.JSONOUTPUT = JSON.stringify(localBlogDetails);
        }
        else {
          console.log("not found in LS");
        }

        // console.clear();
        let paragraphHTML = this.encodeParagraphs(this.blogDetails);
        console.log("Starting HTML Output");
        this.HTMLOUTPUT = "<article role='article' id='hansini'> <input id='lsid' type='hidden' value='" + this.blogDetails.lsid + "'> "
        + this.encodeHiddenSummaryHTML(this.blogDetails)
        + this.buildTOC()
        + this.encodeWholeHTML(this.blogDetails)
        + paragraphHTML
        +  "</article>";
        console.log("Ending HTML Output");
      }
    });
  }

  private encodeParagraphs(blog: BlogDetails) {
    let counter = 1;
    let HTMLOUTPUT = "";
    let meta = blog.meta;
    this.blogDetails.paragraphs.forEach(para => {
      HTMLOUTPUT += this.encodeParagraph(para, counter, meta);
      counter++;
    })
    return HTMLOUTPUT;
  }

  private encodeParagraph(para: BlogParagraph, counter: number, meta:Object) {
    if (para.type == 'H1') {
      let dynaId = this.buildDynamicHTMLID(counter, para.type);
      this.buildAndStoreLink(dynaId, para, meta);
      return "<h1 id='" + dynaId + "'>" + para.heading + "</h1>";
    }
    if (para.type == 'H2') {
      let dynaId = this.buildDynamicHTMLID(counter, para.type);
      this.buildAndStoreLink(dynaId, para, meta);
      return "<h2 id='" + dynaId + "'>" + para.heading + "</h2>";
    }
    if (para.type == 'H3') {
      let dynaId = this.buildDynamicHTMLID(counter, para.type);
      this.buildAndStoreLink(dynaId, para, meta);
      return "<h3 id='" + dynaId + "'>" + para.heading + "</h3>";
    }
    if (para.type == 'SSC') {
      return this.processSimpleStepCodeTypeParagraph(para);
    }
    if (para.type == 'COD') {
      return this.processCodeTypeParagraph(counter, para);
    }
    if (para.type == 'TXT') {
      return "<div class='txt_desc'>" + para.content + "</div>";
    }

    if (para.type == 'VID') {
      return this.processVIDTypeParagraph(counter, para);
    }
    if (para.type == 'IMG') {
      return this.processIMGTypeParagraph(counter, para);
    }
    if (para.type == 'URL') {
      return this.processURLTypeParagraph(counter, para);
    }

    if (para.type == 'LIS') {
      return this.processLISTypeParagraph(counter, para, meta);
    }

    if (para.type == 'CON' ) {
      return this.processCONTypeParagraph(counter, para);
    }
    if (para.type == 'NTE' ) {
      return this.processNTETypeParagraph(counter, para);
    }

    if (para.type == 'GIT') {
      return this.processGitTypeParagraph(counter, para, meta);
    }

  }


  /**
   * Returns <tasklistref><header>title</header><tasks><ul>
   * @param counter
   * @param para
   */
private processLISTypeParagraph(counter: number, para: BlogParagraph, meta: Object) {
    let dynaId = this.buildDynamicHTMLID(counter, para.type);
    this.buildAndStoreLink(dynaId, para, meta);
    let taskArr = para.content.split(/(\r\n|\n|\r)/gm);
    console.log("tasklist splitted size" + taskArr.length);
    let list = "";
    for (let i = 0; i < taskArr.length; i++) {
      if ( taskArr[i].length>1 && taskArr[i]!=' ') {
        list = list + "<li role='listitem'>" + taskArr[i] + "</li>";
      }
    }
    let ret =   "<div class=\"tasklistblock\" id='" + dynaId + "'>"
              +     "<div class=\"headerblock\"><span class='title'>" + para.heading + "</span></div>"
              +     "<div class='bodyblock'>"
              +         "<ul role='list'>"
              +            list
              +         "</ul>"
              +      "</div>"
              +  "</div>"
    console.log("tasklist HTML " + ret);
    return ret ;
  }

  private processIMGTypeParagraph(counter: number, para: BlogParagraph) {
    let ret =   "<div class=\"imgblock\">"
              +     "<div class=\"bodyblock\"> Image Place Holder</div>"
              +  "</div>"
    return ret ;
  }

  private processCONTypeParagraph(counter: number, para: BlogParagraph) {
    let list = "";
    let ret =   "<div class=\"consoleblock\">"
              +     "<div class=\"headerblock\"><span class='title'>" + para.heading + "</span></div>"
              +     "<div class=\"bodyblock\">" + para.content + "</div>"
              +  "</div>"
    console.log("tasklist HTML " + ret);
    return ret ;
  }

  private processNTETypeParagraph(counter: number, para: BlogParagraph) {
    let list = "";
    let ret =   "<div class=\"noteblock\">"
              +     "<div class=\"headerblock\"><span class='title'>" + para.heading + "</span></div>"
              +     "<div class=\"bodyblock\">" + para.content + "</div>"
              +  "</div>"
    console.log("tasklist HTML " + ret);
    return ret ;
  }

  private processGitTypeParagraph(counter: number, para: BlogParagraph, meta:object) {
    //for GIT type heading contains the github URL
    let dynaId = this.buildDynamicHTMLID(counter, para.type);
    this.buildAndStoreLink(dynaId, para, meta);

    let taskArr = para.content.split(/(\r\n|\n|\r)/gm);
    console.log("GIT tasklist splitted " + taskArr.length);
    let body = "";
    for (let i = 0; i < taskArr.length; i++) {
      if ( taskArr[i].length>1 && taskArr[i]!=' ') {
        body = body + "<li class='list-unstyled' >" + taskArr[i] + "</li>";
      }
    }

    let container =   "<div class='codeSummary' id='" + dynaId + "'>"
                    +     "<div class=\"headerblock\">"
                    +        "<span class='title'>"+para.heading+"<span>"
                    +     "</div>"
                    +     "<div class='bodyblock'>"
                    +        "<div class='github'>"
                    +           "<ul>"
                    +              body
                    +           "<ul>"
                    +         "</div>"
                    +     "</div>"
                    + "</div>"
    return container;
  }

  private processURLTypeParagraph(counter: number, para: BlogParagraph) {
    let list = "";
    let ret =   "<div class=\"urlblock\">"
              +     "<div class=\"headerblock\"><span class='title'><a href ='"+ para.content+"' target='_blank'>" + para.heading + "</a></span></div>"
              +  "</div>"
    console.log("tasklist HTML " + ret);
    return ret ;
  }

  private processVIDTypeParagraph(counter: number, para: BlogParagraph) {
    let videoContent = this.blogService.extractVideo(para.content);
    let videoFrame = '<iframe width="560" height="315" src="'+videoContent+'" frameborder="0" allowfullscreen> </iframe>';

    let ret =   "<div class=\"mediafragment\">"
              +     "<div class=\"bodyblock\">" + videoFrame + "</div>"
              +  "</div>"
    return ret ;
  }



  private buildAndStoreLink(targetLink: string, para: BlogParagraph, meta:Object) {
    console.warn("adding a new link for" + para.heading);
    let heading = para.heading;
    if (para.type =='GIT') {
      heading = "Github Source Code"
    }
    if (meta !=null && meta['liveurl'] !='') {
      targetLink = meta['liveurl'] + "#" + targetLink;
    }
    else {
      targetLink = "#" + targetLink;
    }
    let link = '<a href="' + targetLink + '">' + heading + '</a>';
    //this.htmlLinkAnchors.set(para.heading, link);
    this.htmlLinkAnchors.push(link);
    console.warn("this.htmlLinkAnchors size = " + this.htmlLinkAnchors.length);
  }

  private buildDynamicHTMLID(counter: number, elementType: string) {
    return counter + "_" + elementType;
  }

  private encodeWholeHTML(blog: BlogDetails) {
    let dynaId = "mainheading_h2";
    let link = '<a href="#' + dynaId + '">' + blog.name + '</a>';
    //this.htmlLinkAnchors.set(blog.name, link);
    this.htmlLinkAnchors.push(link);
    let heading = "<h2 id='a'>" + blog.name + "</h2>";
    let description = "<div class='description'>" + blog.summary + "</div>";
    return  heading  + description;
  }

  private encodeHiddenSummaryHTML(blog: BlogDetails) {
    let summary = "<div class='summary' style='display:none'>" + blog.summary + "</div>";
    return summary;
  }

  private buildTOC() {
    console.log("buildTOC.....");

    let li = "";

    this.htmlLinkAnchors.forEach(function (value) {
      console.log(value);
      li = li + "<li>" + value + "</li>";
    });

    console.log("build TOC with link size = " + this.htmlLinkAnchors.length);
    let ul =   "<div id='toc' class='toc' role='navigation'>"
              +   "<div class='headerblock'>"
              +      "<span class='title'>Table of Content</span>"
              +   "</div>"
              +    "<div class='bodyblock'>"
              +         "<ul>"
              +             li
              +         "</ul>"
              +    "</div>"
              +"</div>"
    console.log("buildTOC.....Completed");
    return ul;
  }


  /**
   * Returns <CodeFragment><pre><code>
   * @param code
   */
  private processSimpleStepCodeTypeParagraph(code: BlogParagraph) {
    let headBlock = "";
    let descriptionBlock = "";
    if (code.meta['desc'] != "undefined" && code.meta['desc'] != null) {
      descriptionBlock =   '<div class="description">'
                          +   code.meta['desc']
                          + '</div>'
    }

    if (code.heading != "undefined" && code.heading != null) {
      headBlock =   '<div class="headerblock"><span class="title">'
                          +   code.heading
                          + '</span></div>'
    }

    let codePrefix = '<div class="sscfragment">'
                     +    headBlock
                     +    '<div class="bodyblock">'
                     +        descriptionBlock
                     +       '<pre><code>'
                     +           code.content
                     +        '</code></pre>'
                     +     '</div>'
                     +'</div>'
    return codePrefix
  }
  /**
   * Returns <CodeFragment><fileName><pre><code>
   * @param code
   */
  private processCodeTypeParagraph(counter: number, code: BlogParagraph) {
    let content = this.encodeCode(code.content);
    let id = "codeFragment" + counter
    // +'<button class="btn float-sm-right copyBtn" data-clipboard-target="#' + id +'">Copy</button>'

    let codePrefix =   '<div class="codeFragment">'
                     +    '<div class="headerblock">'
                     +       '<span class="title">' + code.heading + "</span>"
                     +    '</div>'
                     +    '<div class="bodyblock">'
                     +        '<pre class="prettyprint w8-100">'
                     +          '<code class="language-' + code.meta['codeLang'] + '"  id="' + id + '">'
                     +             content
                     +          '</code>'
                     +         '</pre>'
                     +    '</div>'
                     +  '</div>';

    return codePrefix;
  }

  private encodeCode(code: String) {
    code = this.encode_entities(code);
    console.log("C= " + code);
    return code;
  }

  private encode_entities(str) {
    var rv = '';
    for (var i = 0; i < str.length; i++) {
      var ch = str.charAt(i); rv += this.char2entity[ch] || ch;
    } return rv;
  }

  private char2entity = { "'": '&#39;', '"': '&quot;', '<': '&lt;', '>': '&gt;', '&#038;': '&amp;' };

}


