import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CreatorComponent} from './creator/creator.component'
import { ListComponent } from './creator/list.component';
import { HtmlResultComponent } from './creator/htmlresult.component';
import { TextAreaAutoSizeComponent } from './extra/textareaautosize.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CreatorLightComponent } from './creator/creatorlight.component';
const routes: Routes = [
  {
    path: '',
    component: ListComponent
  },
  {
    path: 'creatorlight',
    component: CreatorLightComponent
  }
  ,
  {
    path: 'creator',
    component: CreatorComponent
  },
  {
    path: 'clist',
    component: ListComponent
  },
  {
    path: 'htmlview',
    component: HtmlResultComponent
  }
  ,
  {
    path: 'autoresize',
    component: TextAreaAutoSizeComponent
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
